.main-nav__toggle-wrapper {
	width: 30px;
	height: 30px;
	display: flex;
	justify-content: center;
	align-items: center;
	}
.main-nav__items--hidden{
	display: none;
}
.main-nav__toggle {
	&::before {
		position: absolute;
		left: 0;
		top: -10px;
		content: '';
		width: 30px;
		height: 2px;
		background-color: #222222;
		transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
		}
	&::after {
		position: absolute;
		left: 0;
		top: 10px;
		content: '';
		width: 30px;
		height: 2px;
		background-color: #222222;
		transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
	}
	&.main-nav__toggle--active {
		&::before {
			transform: rotate(45deg);
			top: 0;
			transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
		}
		&::after {
			transform: rotate(-45deg);
			top: 0;
			transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
		}
		background-color: transparent;
	}
	position: relative;
	width: 30px;
	height: 2px;
	background-color: #222222;
	}


